.course-certification>.paragraph-feature {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    border-width: 0.0625rem;
    border-radius: 0.25rem;
    background-clip: border-box;
    position: relative;
    z-index: 1;
    width: 100%;
    margin: 0 0 6rem;
    padding: 3.5625rem 1rem 2rem;
    counter-increment: pathway-counter;
    border-width: 0.0625rem;
    background-color: var(--color-header);
    will-change: auto;
    border: 1px solid lightgray;
    background-color: white;
    align-items: center;
}


.course-certification>.paragraph-feature::before {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: -1.5625rem;
    right: 0;
    left: 0;
    width: 3.125rem;
    height: 3.125rem;
    margin: 0 auto;
    content: counter(pathway-counter);
    text-align: center;
    color: var(--achieve-item-bg);
    border-radius: 50%;
    background-color: #e83e8c;
    font-family: var(--font-mono);
    font-size: 1.5rem;
